@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --primary-color: #04009a;
  --seconday-color: #77acf1;
  --light-color: #c0fefc;
  --accent-color: #fad02c;
}
html,
body,
#root {
  width: 100%;
  min-height: 100vh;
  height: 100vh;
  overflow-x: hidden;
}
a {
  text-decoration: none;
  color: inherit !important;
}
section {
  padding: 4rem 0;
}
body {
  background: #d8d8d8;
  color: #333;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}
/* utilities */
.container {
  max-width: 1400px !important;
}
.large-header {
  font-size: 3.5rem;
  font-weight: 900;
}

nav {
  padding: 0 !important;
  background: #f4f4f4;
}
nav a {
  font-size: 1.3rem;
  padding: 2rem 1rem !important;
  transition: all 0.4s !important;
  font-weight: 900;
}
nav a:hover {
  background: var(--seconday-color);
  color: #f4f4f4 !important;
}
.navbar-brand {
  width: 100px;
  padding: 0;
}
.navbar-brand img {
  width: 100%;
}

footer {
  font-size: 1.2rem;
}

#america {
  -webkit-animation: continent alternate 1s infinite;
          animation: continent alternate 1s infinite;
}
#europe {
  -webkit-animation: continent alternate 1s infinite 1s;
          animation: continent alternate 1s infinite 1s;
}

@-webkit-keyframes continent {
  100% {
    fill: var(--accent-color);
  }
}

@keyframes continent {
  100% {
    fill: var(--accent-color);
  }
}
@-webkit-keyframes cursor {
  0% {
  }
}
@keyframes cursor {
  0% {
  }
}

.showcase {
  position: relative;
  min-height: 70vh;
  width: 100%;
  text-align: center;
}
.showcase-inner {
  background: var(--primary-color);
  color: #f4f4f4;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.showcase-inner .large-header {
  z-index: 100;
}
/* svgs */
.plane-svg {
  position: absolute;
  left: 0;
  top: 35px;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.trees-svg {
  position: absolute;
  right: 0px;
  bottom: 100px;
}

/* wave shape background */
.custom-shape-divider-bottom-1619617231 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.custom-shape-divider-bottom-1619617231 svg {
  position: relative;
  display: block;
  width: calc(137% + 1.3px);
  height: 152px;
}

.custom-shape-divider-bottom-1619617231 .shape-fill {
  fill: #d8d8d8;
}

/** For tablet devices **/
@media (min-width: 768px) and (max-width: 1023px) {
  .custom-shape-divider-bottom-1619617231 svg {
    width: calc(155% + 1.3px);
    height: 172px;
  }
}
@media (min-width: 768px) {
  .showcase-inner {
    padding: 0 1rem;
  }
}

:root {
  --card-width: 450px;
}

.offer-card {
  background: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 450px;
  max-width: var(--card-width);
  border-radius: 5px;
  box-shadow: 3px 3px 15px rgba(50, 50, 50, 0.8);
  margin: 0.5rem auto;
}
.offer-card .header {
  position: relative;
}
.offer-card .header img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}
.offer-card .header .category {
  color: #f4f4f4;
  background: rgba(50, 50, 50, 0.8);
  position: absolute;
  top: 30px;
  right: 0;
  padding: 0.5rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 5px 0 0 5px;
}

.offer-card .header .description {
  color: #f4f4f4;
  background: rgba(50, 50, 50, 0.8);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 1rem 0.5rem;
}
.offer-card .header .title {
  font-weight: 600;
  margin-top: 0.2rem;
  font-size: 1.1rem;
}
.offer-card .header .period {
}
.offer-card .body {
  text-align: center;
  background: var(--primary-color);
  color: var(--accent-color);
  padding: 1rem 0.5rem;
}

.offer-card .body .price {
  font-size: 1.2rem;
}
.offer-card .price .price-value {
  display: inline;
  color: #fff;
  font-weight: 700;
}

.offer-card .body .read-more {
  display: inline-block;
  padding: 0.75rem 0.5rem !important;
}

.input-group {
  margin-bottom: 1rem;
}
input {
  display: inline-block;
  min-width: 100%;
  font-size: 1.2rem;
  padding: 0.5rem 0.25rem;
  color: #333;
  border: rgba(50, 50, 50, 0.3);
  box-shadow: 7px 7px 5px rgba(50, 50, 50, 0.1);
}
input.invalid {
  border: 2px solid rgba(236, 77, 77, 0.7);
  outline-color: rgba(236, 77, 77, 0.7);
}
.input-error {
  color: rgba(236, 77, 77, 0.7);
  text-align: center;
  font-size: 1rem;
  margin-top: 0.35rem;
}

form {
  margin-top: 2rem;
}
.switch-mode:hover {
  cursor: pointer;
}

.dashboard-container {
  min-height: 100vh;
}

aside {
  background: #333;
  color: #fff;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

aside a {
  padding: 0 2rem;
  display: inline-block;
  margin: 1rem 0;
  cursor: pointer;
}

aside i {
  color: inherit;
  transition: all 0.4s;
}

aside i:hover {
  color: var(--accent-color);
}



.image-upload {
  text-align: center;
}
.image-upload-box {
  border: rgba(50, 50, 50, 0.3);
  box-shadow: 7px 7px 5px rgba(50, 50, 50, 0.1);
  background: #fff;
  color: #333;
  cursor: pointer;
  display: inline-block;
  font-size: 1.2rem;
  min-width: 30%;
  margin-bottom: 1rem;
  padding: 0.5rem 0.25rem;
}
.image-preview {
  margin-bottom: 1rem;
}
.image-preview img {
  width: 100%;
}

.single-offer {
  margin: 2rem 0;
}

.single-offer img {
  width: 100%;
  height: 500px;
  object-fit: cover;
  object-position: center;
}

